.App {
  max-width: 1280px;
  margin: 0px auto 0px;
  padding: 0px 48px;
}

header {
  margin-top: .75em;
}

header .navbar {
  /* background-color: #504741 !important; */
  padding: 0;
}

header .navbar .nav-link {
  color: #999999 !important;
} 

header .navbar .nav-link:not(:last-child) {
  border-right: 1px solid #999999;
}

#content {
  margin-top: 10px;
  padding: 1em;
  background: white;
  min-height: 90vh;
  display: none;
}

#content.loaded {
  display: block;
}

.hike-list .hike-list-stats {
  font-size: small;
  margin-bottom: .5em;
}

.hike-list > ul  {
  column-width: 30rem;
  margin-left: 5px;
  padding-left:20px;
  font-size: small !important;
}

.hike-list > ul > li { 
  list-style: none; 
  list-style-type: none;
}

.hike-list > ul > li > * {
  margin-right: 5px;
}

